<template>
  <section id="menu">
    <div class="seperator">
      <img src="@/assets/hr2.png" alt="selfpick" />
      <img src="@/assets/hr2-s.png" alt="selfpick" />
    </div>
    <div class="main-content">
      <div class="menu" @click="showModal = true">
        <img src="@/assets/menu.png" alt="selfpick" />
      </div>
    </div>
    <modal v-if="showModal" @close="showModal = false">
      <swiper :options="swiperOption">
        <swiper-slide class="slide-1"></swiper-slide>
        <swiper-slide class="slide-2"></swiper-slide>
        <swiper-slide class="slide-3"></swiper-slide>
        <swiper-slide class="slide-4"></swiper-slide>
        <swiper-slide class="slide-5"></swiper-slide>
        <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
        <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
        <div class="modal-close" @click="showModal = false">
          <img src="@/assets/close.png" alt="selfpick" />
        </div>
      </swiper>
    </modal>
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';
import modal from './Modal';

export default {
  name: 'Introduction',
  data() {
    return {
      scrollReveal: scrollReveal(),
      swiperOption: {
        spaceBetween: 30,
        effect: 'fade',
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        loop: true,
        autoplay: true,
      },
      showModal: false,
    };
  },
  components: {
    modal,
  },
  mounted() {
    this.scrollReveal.reveal('#menu .menu', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  section {
    height: auto;
  }

  #menu {
    position: relative;
    padding: 5% 0;

    .seperator {
      position: absolute;
      top: -20px;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;

      img {
        margin: auto;

        &:nth-child(1) {
          display: none;
        }

        @media screen and (min-width: 768px) {
          &:nth-child(1) {
            display: block;
          }

          &:nth-child(2) {
            display: none;
          }
        }
      }
    }

    .main-content {
      // height: 100vh;
      max-height: 750px;
      position: relative;
      padding: 10%;

      @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .menu {
        position: relative;
        cursor: pointer;
        z-index: inherit;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          width: 100%;
          height: 100%;
          background: url("../assets/menu-finger.png") no-repeat;
          background-size: cover;
        }

        &:hover {
          &::before {
            filter: brightness(5000%);
            -webkit-filter: brightness(5000%);
            -moz-filter: brightness(5000%);
            -o-filter: brightness(5000%);
            -ms-filter: brightness(5000%);
          }
        }
      }
    }

    .swiper-container {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
    }

    .swiper-slide {
      background-position: center;
      background-size: 90% auto;
      background-repeat: no-repeat;

      &.slide-1 {
        background-image: url('../assets/p12.png');
      }
      &.slide-2 {
        background-image: url('../assets/p34.jpg');
      }
      &.slide-3 {
        background-image: url('../assets/p56.jpg');
      }
      &.slide-4 {
        background-image: url('../assets/p78.jpg');
      }
      &.slide-5 {
        background-image: url('../assets/p910.jpg');
      }
    }
  }

  .swiper-button-prev {
    background: url('../assets/go-left.png') no-repeat;
  }

  .swiper-button-next {
    background: url('../assets/go-right.png') no-repeat;
  }

  .modal-close {
    position: absolute;
    top: 1%;
    right: 1%;
    cursor: pointer;
    z-index: 999999;
  }
</style>



