<template>
  <scrollactive>
    <a class="scrollactive-item go-to-home" href="#landing">
      <img src="@/assets/back-to-home.png" />
    </a>
  </scrollactive>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Menu',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.sidebar', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  .go-to-home {
    width: 20px;
    position: fixed;
    top: 20px;
    left: 5%;
    z-index: 100;
    cursor: pointer;

    @media screen and (min-width: 768px) {
      width: 40px;
      left: 10%;
      display: block;
    }
  }
</style>



