<template>
  <div id="nav">
    <nav>
      <scrollactive
        class="nav-container"
        active-class="active"
        :offset="80"
        :duration="800"
        bezier-easing-value=".5,0,.35,1"
      >
        <a
          v-for="item in navItems"
          v-bind:href='item.domId'
          :key="item.id"
          class="scrollactive-item"
        >
          {{ item.name }}
        </a>
      </scrollactive>
    </nav>
  </div>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Menu',
  data() {
    return {
      navItems: [
        { id: 2, name: 'Member', domId: '#member' },
        { id: 3, name: 'Menu', domId: '#menu' },
        { id: 4, name: 'Contact', domId: '#contact' },
      ],
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.sidebar', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  nav {
    position: fixed;
    top: 5%;
    left: 5%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    z-index: 100;
    // display: none;

    @media screen and (min-width: 768px) {
      right: 5%;
      left: -5%;
      display: block;
      text-align: right;
    }

    a {
      position: relative;
      margin-left: 3%;
      color: rgb(148, 106, 41);
      font-size: 14px;
      text-decoration: none;
      cursor: pointer;

      &.active {
        &::before {
          content: '';
          width: 90%;
          height: 1px;
          position: absolute;
          bottom: -10px;
          left: 0;
          right: 0;
          margin: auto;
          background-color: rgb(148, 106, 41);
        }
      }

      @media screen and (min-width: 768px) {
        height: 50px;
        padding-top: 12px;
        font-size: 20px;
        flex-direction: row;
        align-items: center;
      }
    }
  }
</style>



