<template>
  <div id="app">
    <Menu />
    <BackToHome />
    <Sidebar />
    <Landing />
    <Members />
    <Introduction />
    <Contact />
  </div>
</template>

<script>
import Menu from './components/Menu';
import Sidebar from './components/Sidebar';
import BackToHome from './components/BackToHome';
import Landing from './components/Landing';
import Members from './components/Members';
import Introduction from './components/Introduction';
import Contact from './components/Contact';

export default {
  name: 'App',
  components: {
    Menu,
    Sidebar,
    BackToHome,
    Landing,
    Members,
    Introduction,
    Contact,
  },
};
</script>

<style>
@import url(//fonts.googleapis.com/css?family=Muli);
@import url(//fonts.googleapis.com/earlyaccess/notosanskannada.css);

body {
  background-color: rgb(0, 28, 44);
  padding: 0;
  margin: 0;
  font-weight: 300;
  overflow-x: hidden;
}

#app {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
  font-family: 'Noto Sans TC', 'Muli', 'Microsoft JhengHei', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
section {
  width: 100%;
  height: 100vh;
  max-height: 750px;
}
* {
  box-sizing: border-box;
}
img {
  max-width: 100%;
}
button {
  border: none;
  outline:none;
}
span:lang(zh-Hans) {
  font-weight: 800;
}
</style>



