<template>
  <section id="member">
    <div class="seperator">
      <img src="@/assets/hr.png" alt="selfpick" />
      <img src="@/assets/hr-s.png" alt="selfpick" />
    </div>
    <div class="profiles">
      <div
        class="profile"
        v-for="profile in profiles"
        :key="profile.id"
      >
        <div class="content">
        <img v-bind:src="require(`@/assets/${profile.imgUrl}`)" :alt="profile.englishDesc" />
          <div class="desc">
            <div class="chinese-desc">
              <span class="title">{{ profile.chineseTitle }}</span>
              <span class="name">{{ profile.chineseName }}</span>
            </div>
            <div class="english-desc">
              <span class="title">{{ profile.englishTitle }}</span>
              <span class="name">{{ profile.englishName }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Members',
  data() {
    return {
      profiles: [
        {
          id: 1,
          imgUrl: '01-kero.png',
          chineseTitle: '營運長',
          chineseName: '宋天恩',
          englishTitle: 'Chief operating officer',
          englishName: 'Kero Song',
        },
        {
          id: 2,
          imgUrl: '02-ray.png',
          chineseTitle: '店長',
          chineseName: '劉哲瑞',
          englishTitle: 'Store Maneger',
          englishName: 'Ray Liu',
        },
        {
          id: 3,
          imgUrl: '03-jason.png',
          chineseTitle: '調酒師',
          chineseName: '鄭宇笙',
          englishTitle: 'Bartender',
          englishName: 'Jason Zheng',
        },
        {
          id: 4,
          imgUrl: '04-liying.png',
          chineseTitle: '外場正職',
          chineseName: '莊力螢',
          englishTitle: 'Customer Experience Specialist',
          englishName: 'Lilian Zhuang',
        },
        {
          id: 5,
          imgUrl: '05-alston.png',
          chineseTitle: '外場正職',
          chineseName: '賴亭妤',
          englishTitle: 'Customer Experience Specialist',
          englishName: 'Alston Lai',
        },
      ],
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.profiles', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  section {
    // height: 100vh;
    max-height: none;
    height: auto;

    @media screen and (min-width: 1024px) {
      // max-width: calc(180px * 4 + 80px);
      // height: 100vh;
      // max-height: 750px;
      margin: auto;
      padding: 20px 0;
    }
  }

  #member {
    position: relative;

    .seperator {
      position: absolute;
      top: -20px;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;

      img {
        margin: auto;

        &:nth-child(1) {
          display: none;
        }

        @media screen and (min-width: 768px) {
          &:nth-child(1) {
            display: block;
          }

          &:nth-child(2) {
            display: none;
          }
        }
      }
    }
  }

  .profiles {
    position: relative;
    width: 90%;
    margin: 5% auto;
    padding: 5% 0;
    display: flex;
    // align-content: space-around;
    // align-items: center;
    flex-flow: wrap;
    // align-items: start;
    z-index: 5;

    @media screen and (min-width: 1024px) {
      // max-width: calc(180px * 4 + 80px);
      width: 70%;
      min-height: 750px;
      margin: auto;
      flex-direction: row;
    }
  }

  .profile {
    width: 100%;
    margin: 20px 0;

    @media screen and (min-width: 768px) {
      width: 50%;
    }

    .content {
      width: 100%;
      display: flex;
      align-content: center;
      align-items: center;
      justify-content: center;
      flex-direction: row;

      @media screen and (min-width: 768px) {
        // display: block;
      }

      img {
        max-width: 40%;
        display: inline-block;

        @media screen and (min-width: 768px) {
          max-width: 180px;
        }
      }

      .desc {
        width: 50%;
        // margin-top: 20px;
        padding-left: 10px;
        display: inline-block;
        font-size: 0.8em;
        text-align: left;

        @media screen and (min-width: 768px) {
          padding-left: 20px;
          font-size: .9em;
          // text-align: center;
        }

        .chinese-desc {
          margin-bottom: 10px;

          > span {
            color: rgb(148, 106, 41);
          }
        }

        .english-desc {
          > span {
            display: block;
            color: rgb(58, 114, 156);

            @media screen and (min-width: 768px) {
              // display: flex;
            }
          }
        }

        .english-desc, .chinese-desc {
          word-break: normal;

          @media screen and (min-width: 768px) {
            // display: flex;
          }

          span {
            width: 100%;

            @media screen and (min-width: 768px) {
              // width: 50%;
            }

            &:nth-child(1) {
              margin-right: 10px;

              @media screen and (min-width: 768px) {
                // text-align: right;
              }
            }

            &:nth-child(2) {
              // display: ruby;

              @media screen and (min-width: 768px) {
                // margin-left: 10px;

                text-align: left;
              }
            }
          }
        }
      }
    }
  }
</style>



