<template>
  <section id="contact">
    <div class="seperator">
      <img src="@/assets/hr3.png" alt="selfpick" />
      <img src="@/assets/hr3-s.png" alt="selfpick" />
    </div>
    <div class="section5-content">
      <div class="content">
        <a href="https://www.facebook.com/SELF.39/?hc_ref=ARQnMVyK499bq3EtydU9s-h1SBUkF4sSHKced70q_qMeB_9IJmTZh_n_zzZUQAm04Z0&fref=nf
" target="_blank">
          <img src="../assets/01-facebook.png" alt="selfpick.cc" />
        </a>
        <a href="mailto:selfbar39@gmail.com" target="_blank">
          <img src="../assets/02-mail.png" alt="selfpick.cc" />
        </a>
        <a href="selftoken.co" target="_blank">
          <img src="../assets/03-token.png" alt="selfpick.cc" />
        </a>
        <a href="https://jimlin94.io/" target="_blank">
          <img src="../assets/04-selfpick.png" alt="selfpick.cc" />
        </a>
      </div>
    </div>
    <footer>
      <p><span lang="zh-Hans">營業時間 平日 </span>19:00～02:00
      /  <span lang="zh-Hans">假日</span> 19:00～03:00</p>
      <p>Business Hours  Weekday 19:00-02:00 / Holiday 19:00-03:00</p>
      <p><span lang="zh-Hans">每人低消</span> NT350</p>
    </footer>
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Contact',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.section5-content', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  #contact {
    position: relative;
  }

  .seperator {
    position: absolute;
    top: -20px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1;

    img {
      margin: auto;

      &:nth-child(1) {
        display: none;
      }

      @media screen and (min-width: 768px) {
        &:nth-child(1) {
          display: block;
        }

        &:nth-child(2) {
          display: none;
        }
      }
    }
  }

  .section5-content {
    position: relative;
    z-index: 5;

    @media screen and (min-width: 768px) {
      height: 100vh;
      max-height: 750px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .content {
      max-width: 550px;
      width: 100%;
      position: relative;
      margin: auto;
      padding: 10% 0;
      z-index: 2;

      @media screen and (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
      }

      > a {
        width: 40%;
        max-width: 223px;
        height: auto;
        margin: 0 10px;
        padding: 20px;
        display: inline-block;

        @media screen and (min-width: 768px) {
          width: 45%;
          max-width: 223px;
        }

        img {
          width: 100%;
          max-width: 220px;
        }

        &:hover {
          img {
            filter: brightness(5000%);
            -webkit-filter: brightness(5000%);
            -moz-filter: brightness(5000%);
            -o-filter: brightness(5000%);
            -ms-filter: brightness(5000%);
          }
        }
      }
    }
  }

  footer {
    padding-bottom: 80px;
    color: rgb(148, 106, 41);
  }
</style>



