<template>
  <div class="sidebar">
    <a class="sidebar-1" href="https://www.facebook.com/selfpick" target="_blank">
      <img src="@/assets/nav-facebook.png" />
    </a>
    <a class="sidebar-2" href="http://selfpick.cc/articles" target="_blank">
      <img src="@/assets/nav-selfpick.png" />
    </a>
    <a class="sidebar-3" href="https://www.instagram.com/selfpick/" target="_blank">
      <img src="@/assets/nav-selftoken.png" />
    </a>
  </div>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Sidebar',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.sidebar', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  .sidebar {
    position: fixed;
    display: none;
    cursor: pointer;
    z-index: 106;

    @media screen and (min-width: 768px) {
      top: 27vh;
      right: auto;
      left: 3%;
      display: block;
    }

    a {
      display: block;
      margin: 20px 5px;
      flex-direction: column;
      cursor: pointer;

      &:hover {
        img {
          filter: brightness(5000%);
          -webkit-filter: brightness(5000%);
          -moz-filter: brightness(5000%);
          -o-filter: brightness(5000%);
          -ms-filter: brightness(5000%);
        }
      }

      img {
        width: 80px;

        @media screen and (min-width: 768px) {
          width: auto;
        }
      }
    }
  }
</style>



