<template>
  <section id="landing">
    <div class="content">
      <img class="logo" src="@/assets/section1-logo.png" alt="selfpick" />
    </div>
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Landing',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.content img', {
      origin: 'left',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  section {
    position: relative;
    color: #fff;
    overflow: hidden;
  }

  .content {
    height: 100vh;
    max-height: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    h2 {
      position: relative;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 3px;
      text-transform: uppercase;
      z-index: 2;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 5%;
      z-index: 1;
      user-select: none;
    }
  }
</style>



