<template>
  <div class="modal-mask">
    <!-- <div class="modal-wrapper" @click="$emit('close')"> -->
    <div class="modal-wrapper">
      <div class="modal-container">
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'modal',
  data() {
    return {
      showModal: false,
    };
  },
};
</script>

<style lang="scss" scoped>
  .modal-mask {
    position: fixed;
    z-index: 998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    z-index: 999;
  }

  .modal-container {
    max-width: 90%;
    height: 100vh;
    position: relative;
    margin: 0px auto;
    padding: 20px 10px;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    z-index: 999999999;
  }
</style>



